
body {
    margin: 0;
    padding: 0;
}
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin: 0;
    padding: 0;
}

  /*header関連*/
.view-enter-active,
  .view-leave-active {
    -webkit-transition: opacity 0.5s;
    transition: opacity 0.5s;
}
.view-leave-active {
    position: absolute;
}
.view-enter,
  .view-leave-to {
    opacity: 0;
}
.flexcontainer {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    height: 130px;
    background-color: #9999FF;
}
.flexitem {
    width: 33%;
    height: 130px;
    text-align: center;
    font-size: 40px;
    font-weight: bold;
}
.btn-circle-fishy {
    display: inline-block;
    text-decoration: none;
    color: #FFF;
    width: 130px;
    height: 130px;
    line-height: 130px;
    border-radius: 50%;
    text-align: center;
    overflow: hidden;
    background-image: linear-gradient(45deg, #709dff 0%, #91fdb7 100%);
    -webkit-transition: .4s;
    transition: .4s;
}
.btn-circle-fishy:hover {
    -webkit-transform: rotate(10deg);
    transform: rotate(10deg);
}

  /*footer関連*/
footer {
    background-color: #9999FF;
    margin: 0 auto;
}
footer ul {
    margin: 0 auto;
    padding: 0 auto;
}
footer li {
    list-style: none;
    display: inline-block;
}
.btn-circle-fishy-min {
    display: inline-block;
    text-decoration: none;
    color: #FFF;
    width: 50px;
    height: 50px;
    line-height: 50px;
    border-radius: 50%;
    text-align: center;
    overflow: hidden;
    background-image: linear-gradient(45deg, #709dff 0%, #91fdb7 100%);
    -webkit-transition: .4s;
    transition: .4s;
}
.btn-circle-fishy-min:hover {
    -webkit-transform: rotate(10deg);
    transform: rotate(10deg);
}

